@import url(https://fonts.googleapis.com/css2?family=Marvel:wght@400;700&display=swap);
/*
  ==============================================
               ****** RESET *****
  ==============================================
*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  background-image: url(/Space-Travelers-Hub/static/media/space.5a126d68.webp);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  font-family: 'Marvel', sans-serif;
}

.rocket {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  padding-bottom: 20px;
  justify-content: flex-end;
  margin-left: auto;
  margin-right: auto;
}

.rockets-wrapper {
  width: 95%;
  margin: 0 auto;
  margin-top: 3rem;
  max-height: 700px;
  overflow-y: scroll;
}

.rockets-wrapper::-webkit-scrollbar {
  width: 10px;
}

.rockets-wrapper::-webkit-scrollbar-thumb {
  background: rgb(84, 0, 0);
  border-radius: 7px;
}

.rockets-wrapper::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.296);
}

.rockets-wrapper::-webkit-scrollbar-thumb:hover {
  background: rgb(116, 1, 1);
}

.rocket-details {
  width: 70%;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}

.rocket-details h1 {
  margin: 0;
  color: antiquewhite;
  font-size: 22px;
}

.rocket-brief-info {
  color: bisque;
  opacity: 0.7;
  font-size: 18px;
  margin-top: 1rem;
}

.rocket-img-wrapper {
  width: 25%;
}

.rocket-image {
  width: 100%;
  height: 100%;
}

.reservation-btn {
  outline: transparent;
  border: transparent;
  background: rgb(84, 0, 0);
  color: #f9f9f9b8;
  margin-top: 2rem;
}

.cancel-reservation-btn {
  border: 1px solid gray;
  color: gray;
  background-color: #fff;
  margin-top: 2rem;
}

.reservation-btn,
.cancel-reservation-btn {
  padding: 15px;
  font-weight: bold;
  border-radius: 4px;
}

.reserved-rocket {
  background-color: rgb(255, 1, 1);
  margin-right: 8px;
  font-size: 15px;
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
  padding: 2px 10px;
}

.logo {
  width: 80px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.535);
}

.logo-title-wrapper {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  cursor: pointer;
}

.nav-title,
.link {
  color: rgb(133, 4, 4);
}

.nav-list {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  font-size: 24px;
}

.nav-rockets {
  padding-right: 2rem;
}

.nav-my-profile {
  border-left: 1px solid #000;
  padding-left: 2rem;
}

.link:focus {
  text-decoration: underline;
}

.missions-table {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
  max-height: 700px;
  overflow-y: scroll;
}

.missions-table::-webkit-scrollbar {
  width: 10px;
}

.missions-table::-webkit-scrollbar-thumb {
  background: rgb(84, 0, 0);
  border-radius: 7px;
}

.missions-table::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.296);
}

.missions-table::-webkit-scrollbar-thumb:hover {
  background: rgb(116, 1, 1);
}

table {
  border-collapse: collapse;
  width: 100%;
  border-bottom: 2px solid #d3d3d3;
}

td,
th {
  border: 2px solid black;
  text-align: left;
  padding: 8px;
}

th {
  vertical-align: top;
  font-size: 20px;
}

tr {
  color: bisque;
}

tr:nth-child(even) {
  background-color: rgba(116, 1, 1, 0.59);
}

tr:first-child > th {
  color: antiquewhite;
}

.status-placeholder {
  background-color: rgba(0, 0, 0, 0.529);
  border-radius: 7px;
  padding: 0.5rem;
  white-space: nowrap;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
}

.description {
  font-size: 18px;
  opacity: 0.7;
}

.join-btn,
.leave-btn {
  white-space: nowrap;
  padding: 0.5rem;
  font-size: 18px;
  width: 150px;
  border-radius: 4px;
  outline: transparent;
  border: transparent;
  cursor: pointer;
}

.join-btn {
  background: rgb(192, 1, 1);
  color: #fff;
}

.leave-btn {
  background: #fff;
  color: rgb(192, 1, 1);
}

.wrapper {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
}

.my-profile-wrapper {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
}

.my-profile-wrapper h2 {
  color: antiquewhite;
}

.title {
  margin-bottom: 0.5rem;
}

.list {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.467);
  color: bisque;
  box-shadow: rgba(0, 0, 0, 0.35) 0 5px 15px;
}

.list > li:nth-child(even) {
  background-color: rgba(116, 1, 1, 0.59);
  font-weight: bold;
}

.filter-names {
  border: 1px solid black;
  padding: 1rem;
}

